import React, { useState, useEffect } from 'react';

const TypistText = ({ texts, typingSpeed = 100 }) => {
    const textArray = typeof texts === 'string' ? [texts] : texts;
    const isSingleString = textArray.length === 1;

    const [index, setIndex] = useState(0);
    const [subIndex, setSubIndex] = useState(0);
    const [blink, setBlink] = useState(true);
    const [reverse, setReverse] = useState(false);
    const [isTypingComplete, setIsTypingComplete] = useState(false);

    useEffect(() => {
        if (isSingleString && subIndex === textArray[0].length && !reverse) {
            // Stop the blinking cursor once the single string is completely typed
            setIsTypingComplete(true);
            return;
        }

        if (index >= textArray.length) {
            setIndex(0);
            return;
        }

        if (subIndex === textArray[index].length + 1 && !reverse) {
            setTimeout(() => setReverse(true), 1500);
            return;
        }

        if (subIndex === 0 && reverse) {
            if (!isSingleString) {
                setIndex((prev) => prev + 1);
            }
            setReverse(false);
            return;
        }

        const timeout = setTimeout(() => {
            setSubIndex((prev) => prev + (reverse ? -1 : 1));
        }, typingSpeed);

        return () => clearTimeout(timeout);
    }, [subIndex, index, reverse, textArray, typingSpeed, isSingleString]);

    useEffect(() => {
        if (isTypingComplete) {
            return;
        }

        const blinkTimeout = setTimeout(() => {
            setBlink((prev) => !prev);
        }, 500);

        return () => clearTimeout(blinkTimeout);
    }, [blink, isTypingComplete]);

    return (
        <div className="TypistText">
            {`${textArray[index]?.substring(0, subIndex)}${isTypingComplete ? '' : (blink ? '|' : ' ')}`}
        </div>
    );
};

export default TypistText;
