import React from 'react'

const Link = (props) => {
	return (
		<a
			onClick={() => (props.onClick ? props.onClick() : undefined)}
			target={props.target || '_blank'}
			href={props.href || '#'}
			rel={'noopener noreferrer'}
			className={`font-bold text-burgundy ${
				props.noHover ? '' : props.inverted ? 'hover:bg-transparent' : 'hover:text-burgundy hover:underline'
			} transition-colors duration-100 pointer-events-auto text-burgundy ${props.className}`}
		>
			{props.children}
		</a>
	)
}

export default Link
